body {
  font: 14px "akzidenz-grotesk_proregular";
  margin: 0;
  padding: 0;
}

a:-webkit-any-link {
  color: black;
  cursor: pointer;
}

#navbar {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 60px;
  top: 0;
    z-index: 9999;
}

@media (max-width: 481px) { 
  #navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100vw;

  }
}

.btnAuth {
  border: none;
  background: none;
  cursor: pointer;
  padding: 0px;
}

@media (max-width: 481px) { 
  .btnAuth {
    display: none;
  }
}

.btnAuth:active {
  outline: none;
  border: none;
}

