@media (min-width: 481px) {
#navtitles { 
  padding: 0; 
  list-style: none; 
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-right: 1.5%;
  text-align: center;
  height: 35px;
  color: black;
}
}
@media (min-width: 481px) {
#navtitles > li { 
  display: table-cell; 
  position: relative; 
  padding: 15px 0;
  text-decoration: none;
  color: black ;
}
}

@media (max-width: 481px) { 
  #navtitles { 
  display: none;
  }
}Â 


.link {
  color: black;
  text-transform: uppercase;
  text-decoration: none; 
  display: inline-block;
  position: relative;
  font-size: 13px;
}

#navtitles > li > a:after {    
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 1px;
  left: 50%;
  position: absolute;
  background:black;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
  margin-top:7px;
}

#navtitles > li > a:hover:after { 
  width: 100%; 
  left: 0; 
}

.activeLink{
  font-family: 'akzidenz-grotesk_probold';
  border-bottom: 1px solid #000;
  padding-bottom: 0px;
}

#navtitles > li > .activeLink:hover:after { 
  width: 0%; 
  left: 0; 
}


#btnMenuMobile{
  display: none;
}

@media (max-width: 481px) { 
  #btnMenuMobile{
    display: block;
  margin-right: 20px ;
    border: 1px solid black;
    background: none;
    cursor: pointer;
    padding: 5px 10px
  }
}