.colectorContainer{
  display: flex;
  flex-direction: column;
  justify-content:flex-start;
  align-content: space-between;
}
#headerText {
  display: flex;
  color: white;
  flex-direction: column;
  justify-content: flex-start;
  padding-left: 13px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}

#headerText h1{
  font-size: 30px;
  font-family: "editorial_newultralight";
  margin:0;
}

#headerText h2 {
  font-family: "akzidenz-grotesk_proregular";
  font-size: 12px;
  text-transform: uppercase;
}

#headerText p {
  font-size: 12px;
  font-family: 'akzidenz-grotesk_proregular';
  margin: 0;
}

.header{
  display: flex;
  background-color: #D2D1CE;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
}

@media (min-width: 480px) {
  .header .btnBlack {
    color: white;
    font-size: 14px;
    padding: 12px 24px;
    border: solid 1px white;
    cursor: pointer;
    background-color: transparent;
    width: 300px;
    text-decoration: none;
    margin-top: 2%;
    margin-bottom: 2%;
    margin-right: 20px;
  }
    }
  
    @media (max-width: 481px) {
      .header .btnBlack {
      display: none;
      }
    }
  
    .header .btnBlack:hover {
    background-color: black;
    color: white
  }

#collectorMain {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  padding-bottom: 1rem;
  animation: fadeEffect 1s; /* Fading effect takes 1 second */
  
}
.bg-services {
  background-image: url('../../../images/COLLECTOR\ SPACE\ 2.jpg'), url('../../../images/CONNECTOR\ SPACE\ 5.jpg');
  background-size: 350px auto;
  background-position: center left -10%, center right -10%;
  background-repeat: no-repeat;
  animation: fadeEffect 1s; /* Fading effect takes 1 second */
}
.bg-special {
  background-image: url('../../../images/COLLECTOR\ SPACE\ 3.jpg'), url('../../../images/COLLECTOR\ SPACE\ 1.jpg');
  background-size: 350px auto;
  background-position: center left -10%, center right -10%;
  background-repeat: no-repeat;
  animation: fadeEffect 1s; /* Fading effect takes 1 second */
}

/* Style the tab */
.tabs {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  width: 60vw;
  height: 70vh;
}
.tab {
  overflow: hidden;
}

/* Style the buttons that are used to open the tab content */
.tab button {
  color:  #C8C8C8;
  background:inherit;
  float: center;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  text-transform: uppercase;
  font-family: "akzidenz-grotesk_proregular";
  font-size: 23px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: justified;
}

/* Change background color of buttons on hover */
.tab button:hover {
  text-decoration: underline;
  color: #000;
}

/* Create an active/current tablink class */
.tab button.active {
  color: #000;
  font-family: "akzidenz-grotesk_proregular";
  font-size: 23px;
  font-style: normal;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: justified;
  text-decoration: underline;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  /* border: 1px solid #ccc; */
  border-top: none;
  animation: fadeEffect 1s; /* Fading effect takes 1 second */
}
.tabcontent h2 {
  font-family: 'editorial_newregular', sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: justified;
  text-transform: uppercase;
}
.tabcontent ul {
  list-style: none;
  font-family: "akzidenz-grotesk_proregular";
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
  text-transform: uppercase;

}
.tabcontent ul li span {
  font-family: Akzidenz-Grotesk Pro;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
}
.tabtext {
  font-family: "akzidenz-grotesk_proregular";
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 0em;
  text-align: justified;
  text-transform: uppercase;
}
.tabtext span {
  font-family: "akzidenz-grotesk_probold";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
  letter-spacing: 0em;
  text-align: justified;
}

/* Go from zero to full opacity */
@keyframes fadeEffect {
  from {opacity: 0;}
  to {opacity: 1;}
}

.boxes {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-content: stretch;
  align-items: stretch;
}
.boxes .personal {
  font-weight: 300;
  padding-right: 70px;
  width: 300px;
}
.personal h3 {
