}

.partBtn {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 260px;
  color: white;
  font-size: 14px;
  padding: 12px 10px;
  cursor: pointer;
  background-color: black;
  text-transform: uppercase;
  font-family: "akzidenz-grotesk_proregular";
  font-style: normal;
  font-weight: 400;
  line-height: 25px; 
